 * {
    font-family: 'Roboto', sans-serif;
    margin: 0;
    overflow-x: hidden;
  }


.app{
    height: 100vh;

    .sections{
        width: 100%;
        height: calc(100vh - 70px);
        background-color: red;
        position: relative;
        top: 70px;
        scroll-snap-type: y mandatory;
        scroll-behavior: smooth;
        scrollbar-width: none; // for firefox
        &::-webkit-scrollbar{
            display: none ;
        }
    
        > *{
            width: 100vw;
            height: calc(100vh - 70px);
            scroll-snap-align:start ;
        }
    }
}
